.iconTemp {
  /* border: 3px solid black; */
  width: 400px;
}
.iconTemp div {
  /* background-color: red; */
  /* border: 2px solid blue; */
  height: 100px;
  width: 350px;
  display: flex;
  justify-content: left;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px;
  margin: 12px;
  border-radius: 12px;
  padding-left: 18px;
  transition: all 0.6s;
}
.iconTemp div:hover {
  transform: scale(1.05);
}
.iconImg {
  /* border: 2px solid black; */
  margin-right: 15px;
}
.iconTitle {
  font-size: 20px;
}

