.project {
  /* border: 4px solid black; */
  /* position: relative; */
  height: 335px;
  width: 1000px;
  margin-top: 50px;
  margin-bottom: 60px;
  display: flex;
  align-items: center;
}
.leftBox {
  /* border: 3px solid blue; */
  width: 50%;
}
.imgDiv {
  /* border: 2px solid green; */
  width: 580px;
}
.imgDiv img {
  /* border: 2px solid black; */
  border-radius: 8px;
  transition: all 0.6s;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
}
.imgDiv img:hover {
  transform: scale(1.1);
}
.rightBox {
  /* border: 3px solid rgba(39, 92, 28, 0.929); */
  height: 300px;
  justify-content: right;
  text-align: right;
  width: 50%;
}
/* .titleDiv {
  border: 2px solid red;
  width: 500px;
  padding-right: 20px;
} */
.titleDiv p {
  color: black;
  font-size: 40px;
  font-weight: 900;
}
.descriptionDiv {
  /* border: 2px solid red; */
  background-color: black;
  width: 430px;
  padding: 20px;
  margin-left: 65px;
  margin-right: 0px;
  text-align: right;
  border-radius: 10px;
}
.descriptionDiv p {
  color: white;
}
.techStacksDiv {
  /* border: 2px solid blue; */
  display: flex;
  justify-content: right;
}
.techStacksDiv div {
  /* border: 2px solid pink; */
  background-color: white;
  width: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px;
  margin: 12px;
  border-radius: 12px;
  transition: all 0.6s;
  padding: 8px;
}
.techStacksDiv div:hover {
  transform: scale(1.05);
}

/* .techStacksDiv div img {
  border: 2px solid yellow;
} */

/* .techStacksDiv p {
  font-size: 18px;
  font-weight: bolder;
  color: black;
} */
.btnDiv {
  /* border: 2px solid red; */
  display: flex;
  justify-content: right;
  width: 340px;
  justify-content: space-between;
  margin-left: 160px;
}
.btnGit {
  height: 32px;
  border-radius: 50px;
  padding-right: 15px;
  padding-left: 15px;
  color: white;
  width: 140px;
  font-size: 18px;
  background-color: #0071e3;
  display: flex;
  align-items: center;
  transition: all 0.6s;
}
.btnGit img {
  /* background-color: #0071e3; */
  height: 30px;
  margin-right: 10px;
}
.btnGit:hover {
  /* background: #0c86ff; */
  transform: scale(1.05);
}
.btnLink {
  height: 32px;
  border-radius: 50px;
  padding-right: 15px;
  padding-left: 15px;
  color: white;
  width: 190px;
  font-size: 18px;
  background-color: #0071e3;
  display: flex;
  align-items: center;
  transition: all 0.6s;
}
.btnLink img {
  background-color: #0071e3;
  height: 24px;
  margin-right: 10px;
}
.btnLink:hover {
  /* background: #0c86ff; */
  transform: scale(1.05);
}

@media only screen and (max-width: 1200px) {
}

@media only screen and (max-width: 1000px) {
  .project {
    width: 100%;
  }
  .imgDiv {
    width: 480px;
  }
  .rightBox {
    justify-content: right;
    text-align: right;
  }
  .descriptionDiv {
    width: 485px;
    margin-right: 0px;
    margin-left: -30px;
  }
  .techStacksDiv {
    margin-right: 0px;
  }
  .btnDiv {
    margin-left: 120px;
  }
}

@media only screen and (max-width: 600px) {
  /*Big smartphones [426px -> 600px]*/
  .project {
    /* border: 2px solid red; */
    width: 95%;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .leftBox {
    width: 100%;
  }
  .imgDiv {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
  }
  .imgDiv img {
    width: 90%;
  }
  .rightBox {
    justify-content: right;
    text-align: right;
    width: 90%;
  }
  .titleDiv {
    text-align: left;
  }
  .titleDiv p {
    font-size: 30px;
  }
  .descriptionDiv {
    width: 100%;
    margin-right: 0px;
    margin-left: 0px;
    text-align: left;
  }
  .descriptionDiv p {
    font-size: 12px;
  }
  .techStacksDiv {
    /* margin-right: 0px; */
    justify-content: left;
  }
  .btnDiv {
    justify-content: space-evenly;
    width: 100%;
    margin-left: 0px;
  }
}

@media only screen and (max-width: 425px) {
  /*Small smartphones [325px -> 425px]*/
  .project {
    /* border: 2px solid red; */
    width: 95%;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .leftBox {
    width: 100%;
  }
  .imgDiv {
    width: 100%;
    display: flex;
    justify-content: center;
  }
  .imgDiv img {
    width: 90%;
  }
  .rightBox {
    justify-content: right;
    text-align: right;
    width: 90%;
  }
  .titleDiv {
    text-align: left;
  }
  .titleDiv p {
    font-size: 30px;
  }
  .descriptionDiv {
    width: 100%;
    margin-right: 0px;
    margin-left: 0px;
    text-align: left;
  }
  .descriptionDiv p {
    font-size: 12px;
  }
  .techStacksDiv {
    /* margin-right: 0px; */
    justify-content: left;
  }
  .btnDiv {
    justify-content: space-between;
    width: 100%;
    margin-left: 0px;
  }
}
